.iptv-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  .profile_con {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .profile_card {
      width: 100px;
      height: 100px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border: red solid 2px;

      &:hover {
        cursor: pointer;
        background-color: #fff;
        color: #252525;
      }
      h2 {
        margin: 0;
      }
      img {
        width: 50px;
      }
    }
  }
}
.selected {
  background-color: #fff;
  color: #252525;
}