.seriesData {
  width: 75%;
  height: 100vh;
  margin: 0 auto;
  .seriesData_info {
    background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    width: 100%;
    height: 50%;
    display: flex;
    color: #ffffff;
    position: relative;
    z-index: 1;
    .seriesData_info_text {
      width: 90%;
      height: 100%;
      padding: 0 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .seriesData_info_text_title {
        font-size: 30px;
        font-weight: bold;
        margin-bottom: 20px;
      }
      .seriesData_info_text_genre {
        width: 40%;
        text-align: center;
      }
      .seriesData_info_text_info {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        .seriesData_info_text_rating {
          span {
            padding-left: 10px;
          }
        }
        p {
          text-align: center;
          width: 30%;
          font-size: 20px;
          font-weight: bold;
          margin-bottom: 20px;
        }
      }
      .seriesData_info_text_description {
        height: 45%;
        overflow-x: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
          display: none;
        }
        font-size: 20px;
        margin-bottom: 20px;
        width: 100%;
      }
      .seriesData_info_text_buttons {
        display: flex;
        justify-content: space-around;
        width: 100%;
        button {
          width: 30%;
          height: 50px;
          margin-top: 50px;
          background-color: #383838;
          color: #ffffff;
          font-size: 20px;
          font-weight: bold;
          border: none;
          border-radius: 10px;
          cursor: pointer;
          transition: 0.3s;
          &:hover {
            background-color: #ffffff;
            color: #383838;
          }
        }
      }
    }

    .seriesData_image {
      width: 100%;
      height: 100%;
      text-align: end;
      z-index: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 80%;
        height: 50%;
        max-width: 300px;
        object-fit: contain;
      }
    }
  }
  .seriesData_seasons {
    width: 100%;
    height: 30%;
    background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    overflow-x: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0;
    position: relative;
    z-index: 1;
    .seasons {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-left: 10px;
      margin-right: 10px;
      margin-top: 10px;
      margin-bottom: 10px;
      cursor: pointer;
      &:hover {
        transform: scale(1.1);
        transition: transform 0.5s;
      }
      .seasons_icon {
        width: 220px;
        height: 300px;
        img {
          border-radius: 10px 10px 0 0;
          width: 220px;
          height: 300px;
        }
      }
      .seasons_name {
        width: 200px;
        padding: 10px;
        margin: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: 16px;
        font-weight: bold;
        background-color: #ff0000;
        border-radius: 0 0 10px 10px;
      }
    }
  }
  .seriesData_info_details {
    width: 100%;
    height: 20%;
    display: flex;
    padding: 0 50px;
    color: #ffffff;
    background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    position: relative;
    z-index: 1;
    h2 {
      margin: 0;
    }
    div {
      width: 20%;
      height: 200px;
      text-align: center;
      font-size: 20px;
    }
  }
  .series_episodes_con {
    width: 85%;
    height: 100%;
    background-color: #000000ae;
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    .series_episodes {
      width: 80%;
      height: 80%;
      background: #ff0000;
      overflow: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        display: none;
      }
      .episodes_data {
        width: 100%;
        display: flex;
        justify-content: space-around;
        background: #303030;
        border: 1px black solid;
        padding: 10px;
        cursor: pointer;
        div {
          width: 20%;
          margin: 0;
          text-align: center;
        }
      }
    }
  }
}
