.live-categories-container {
  width: 90%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #252525;
  .iptv-player {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .live-categories-list {
    width: 50%;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    .live-categories {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin: 0;
      padding: 0;
      .category-item {
        width: 100%;
        height: 25px;
        display: flex;
        justify-content: space-between;
        padding: 10px;
        list-style-type: none;
        text-align: start;
        font-size: large;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        border-bottom: 2px solid #ff0000;
        border-right: 2px solid #ff0000;
        font-size: large;
        font-weight: bold;
        cursor: pointer;
        &:hover {
          background: #ff0000;
          color: #ffffff;
        }
        
        .category-name {
          display: flex;
          align-items: center;
        }
        .category-count {
          display: flex;
          align-items: center;
        }
      }
    }
  }
  .live-stream-item {
    width: 50%;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    .live-streams {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin: 0;
      padding: 0;
    }
    .stream-item {
      width: 100%;
      height: 25px;
      display: flex;
      justify-content: space-between;
      padding: 10px;
      list-style-type: none;
      text-align: start;
      font-size: large;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      border-bottom: 2px solid #ff0000;
      border-right: 2px solid #ff0000;
      font-size: large;
      font-weight: bold;
      cursor: pointer;
      &:hover {
        background: #ff0000;
        color: #ffffff;
      }
      img {
        margin-right: 10px;
      }
      .add-favorite {
        display: flex;
        align-items: center;
      }
    }
  }
}