.carusel_container {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    .carusel {
      display: flex;
      transition: transform 1s ease-in-out;
      height: 100%;
      .carusel_item {
        min-width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
      }
    }
  }