.series_categories_container{
  width: 90%;
  height: 100%;
  background-color: #252525;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  .series_categories_list_container{
    width: 100%;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
    .series_categories{
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      margin: 0;
      padding: 0;
      .series_category{
        width: 100%;
        list-style: none;
        cursor: pointer;
        .category-name {
          display: flex;
          justify-content: space-between;
          font-size: 20px;
          font-weight: bold;
          margin-left: 10px;
          .category-count {
            padding-right: 20px;
          }
        }
        &:hover{
          background-color: #ff0000;
        }
      }
      .series_data_list{
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
        position: fixed;
        top: 0;
        width: 85%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0, 0, 0, 0.8);
        .series_list{
          width: 60vw;
          height: 70vh;
          background-color: #ffffff;
          overflow-x: auto;
          -ms-overflow-style: none;
          scrollbar-width: none;
          &::-webkit-scrollbar {
            display: none;
          }
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          align-items: center;
          padding: 0;
          .series{
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-left: 10px;
            margin-right: 10px;
            margin-top: 10px;
            margin-bottom: 10px;
            cursor: pointer;
            &:hover {
              transform: scale(1.1);
              transition: transform 0.5s;
            }
            .series-icon{
              width: 220px;
              height: 300px;
              img {
                border-radius: 10px 10px 0 0;
                width: 220px;
                height: 300px;
              }
            }
            .series_name{
              width: 200px;
              padding: 10px;
              text-align: center;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              font-size: 16px;
              font-weight: bold;
              background-color: #ff0000;
              border-radius: 0 0 10px 10px;
            }
            .favorite_icon {
              width: 50px;
              height: 50px;
              position: relative;
              bottom: 330px;
              left: 90px;
              cursor: pointer;
            }
          }
      }
      }
      
    }
  }
}
