.list-division {
  width: 10%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 0;
  left: 0;
  z-index: 100;
  transition: all 0.5s;
  box-shadow: 0 0 5px 0 rgba(255, 255, 255, 0.5);
  .logo {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    box-shadow: 0 0 10px 0 rgba(255, 255, 255, 0.5);
    cursor: pointer;
    img {
      width: 50px;
      height: 50px;
    }
  }
  .division {
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    color: #ffffff;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    &:hover {
      background: #333;
      color: #fff;
    }

    img {
      width: 50px;
      height: 50px;
    }
  }

}
