.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.main_error {
  background: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

.error {
  color: red;
  font-weight: bold;
}

.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 40px;
  font-weight: bold;
  line-height: 1;
  background-color: white;
  color: black;
  border: none;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;

  &:hover {
    background-color: rgb(255, 0, 0);
    color: rgb(255, 255, 255);
  }
}
